<template>
  <div id="app">
    <div class="botupchat" :style="UIPrefs.chat.css">
      <Header :conversationHeader="true"></Header>
      <div :style="UIPrefs.min_heightconv.css">
        <div v-if="!conversationList.length">
          <div class="bg-white mx-auto pb-3 text-center">
            <img src="" width="190px" class="img-fluid mt-5 mx-auto pt-4" />
            <p class="d-block text-black-50 mt-4 small">
              {{ $t("botupChat.empty_state") }}
            </p>
          </div>
        </div>
        <!-- user bot list starts here -->
        <div v-else>
          <b-row class="ml-0 mr-0 pb-1" v-if="conversationList.length">
            <b-card no-body class="w-100 border-0 p-0">
              <b-card-body class="my-n3 o-cardbody">
                <b-list-group class="list-group-flush">
                  <b-list-group-item class="m-0 pb-2 pl-0 pr-0 pt-2 mt-1 border-bottom" v-for="(item, index) in conversationList" v-bind:key="index">
                    <b-row class="align-items-top">
                      <b-col cols="auto" class="pr-2">
                        <div class="avatar text-uppercase avatar-sm">
                          <b-img :src="UIPrefs.chat_header.attr.gravatar" class="botupchat-profile" :style="UIPrefs.chat_header.css.img"></b-img>
                        </div>
                      </b-col>
                      <b-col class="pl-0 pr-0">
                        <h4 class="mb-0 ellipsis heading-text text-capitalize" style="cursor: pointer;font-size:14px;" @click="conversations(item)">
                          {{ $t("botupChat.default_bot_name") }}
                        </h4>
                        <p class="card-text datetime small lineheight text-muted" style="font-size:9px;">
                          <time>
                            {{ dateFormat(new Date().toLocaleDateString()) }}
                          </time>
                        </p>
                      </b-col>
                    </b-row>
                  </b-list-group-item>
                </b-list-group>
              </b-card-body>
            </b-card>
          </b-row>
          <!-- end -->
        </div>
      </div>
      <Footer :conversationFooter="true"></Footer>
    </div>
  </div>
</template>
<script>
  import moment from "moment";
  import Header from "./Window/Header.vue";
  import Footer from "./Window/Footer.vue";
  export default {
    components: { Header, Footer },
    data() {
      return {
        conversationList: [],
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    created() {},
    mounted() {
      this.conversationList = window._Botup.State.getters.getAllConversations;
    },
    methods: {
      // Format current date like MMM DD,YYYY
      dateFormat(date) {
        return moment
          .utc(date.replace(/(\d+)(st|nd|rd|th)/, "$1"))
          .local()
          .format("MMM DD,YYYY");
      },

      // Get the details with respective subject
      conversations(value) {
        // To remove active conversation from vuex
        window._Botup.State.commit("removeActiveConversation", []);

        // To set active conversation ID which is selected from history by visitor
        window._Botup.Chat.conversation.setActiveConversationID(Object.keys(value)[0]);

        // Get active conversation ID
        var conversationID = window._Botup.State.getters.getActiveConversationID;

        // Get conversation from history based on active conversation ID
        var result = this.conversationList.filter((item) => {
          return Object.keys(item)[0] == conversationID;
        });

        // To set active conversation
        result[0][conversationID].forEach((obj) => {
          window._Botup.State.commit("setMessages", obj);
        });

        // Redirects to chat
        this.$router.push({
          name: "chat",
          params: { active: true },
        });
      },
    },
  };
</script>



